body {
  margin: 0;
  padding: 0;
  background: #1e1e1e;
  color: white;
}
.nav {
  color: white;
  box-sizing: border-box;
  padding: 15px;
  background: #1e1e1e;
  border-bottom: 1px solid #6d2fff;
  display: flex;
  gap: 20px;
}
.navElement {
  padding: 5px 10px;
  border-radius: 10px;
  text-decoration: none;
  background: #ffffff;
  transition: all 0.5s;
}
.navElement:hover {
  color: #ffffff;
  background: #0c92ff;
}

.match {
  box-sizing: border-box;
  margin: 4vh;
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  font-family: Impact;
}
.match span {
  font-size: 40px;
  animation: fade 1.5s linear alternate;
}
.match img {
  box-sizing: border-box;
  width: 100%;
  max-width: 800px;
  border-radius: 20px;
  filter: hue-rotate(20deg);
  animation: fade 1s linear alternate;
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
