.results {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  gap: 10px;
  justify-content: center;
}
.playerCard {
  width: 100%;
  margin: auto;
  max-width: 800px;
  box-sizing: border-box;
  background: #2a2a2a;
  color: #ffffff;
  border-radius: 100px 10px 10px 100px;
  display: flex;
  gap: 10px;
  padding: 10px;
  transition: all 0.5s;
}
.playerCard:hover {
  background: #3b3b3b;
}
.pos {
  display: grid;
  place-items: center;
  min-width: 80px !important;
  min-height: 80px !important;
  background: #0c92ff;
  border-radius: 100%;
  font-size: 40px;
  font-family: Impact;
}
.teamData {
  width: 100% -100px;
  height: 80px;
}
.teamName {
  font-family: Impact;
  font-size: 25px;
}
.teamPlayers {
  color: #878787;
}

@media (max-width: 400px) {
  .pos {
    min-width: 70px !important;
    min-height: 70px !important;
  }
  .teamData {
    height: 70px;
  }
  .teamName {
    font-family: Impact;
    font-size: 18px;
  }
  .teamPlayers {
    font-size: 11px;
  }
}

.results {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.fade-in {
  opacity: 0;
  transform: translateX(100px); /* Optional: Add a slight slide effect */
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}

.fade-in.visible {
  opacity: 1;
  transform: translateX(0); /* Reset slide effect */
}
